<template>
<div>
    <div class="pos">
        <el-form :model="ruleForm2" :rules="rules" ref="ruleForm2" label-width="100px" class="demo-ruleForm">
            <h2>zero-error点单系统</h2>
            <el-form-item label="账号" prop="username">
                <el-col :span="24">
                    <el-input v-model="ruleForm2.username"></el-input>
                </el-col>
            </el-form-item>

            <el-form-item label="密码" prop="password">
                <el-col :span="24">
                    <el-input type="password" v-model="ruleForm2.password" autocomplete="off" show-password></el-input>
                </el-col>
            </el-form-item>

            <el-form-item>
                <el-button type="primary" round @click="submitForm2('ruleForm2')">登录</el-button>
            </el-form-item>
        </el-form>
    </div>
     <router-link to="/admin_login">进入后台管理系统</router-link>
</div>
</template>

<style scoped>
.pos {
    position: absolute;
    background-color: aliceblue;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}
h2{
   text-align: center;
   font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}
input {
    display: block;
    width: 250px;
    height: 40px;
    line-height: 40px;
    margin: 0 auto;
    margin-bottom: 10px;
    outline: none;
    border: 1px solid #888;
    padding: 10px;
    box-sizing: border-box;
}

button {
    display: block;
    width: 250px;
}

span {
    cursor: pointer;
}

span:hover {
    color: #41b883;
}

.el-main {
    background-color: #E9EEF3;
    color: #333;
    text-align: center;
    line-height: 160px;
}

.el-form{
  border: #333 2px solid;
  width: 400px;
  border-radius: 4px;
  padding: 40px 40px;
}
</style>

<script scoped>
import {employeesLogin} from '@/api/user'
export default {

  data () {
    var checkTel = (rule, value, callback) => {
      if (!value) {
        return callback(new Error('手机号不能为空'))
      }
      setTimeout(() => {
        // 匹配13，14，15，16,17,18开头的手机号码！
        var reg = /^0?1[3|4|5|6|7|8][0-9]\d{8}$/
        if (!reg.test(value)) {
          callback(new Error('请输入正确的手机号码！'))
        } else {
          callback()
        }
      }, 1000)
    }
    var validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'))
      } else {
        if (this.ruleForm2.password !== '') {
          // this.$refs.ruleForm.validateField('checkPass');

        }
        callback()
      }
    }
    return {
      ruleForm2: {
        username: '',
        password: ''
      },
      employees:[],
      rules: {
        username: [{
          required: true,
          validator: checkTel,
          trigger: 'blur'
        }],
        password: [{
          required: true,
          validator: validatePass,
          trigger: 'blur'
        }]
      }
    }
  },
  methods: {
    submitForm2 (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          let _this = this
          // const url = 'http://120.79.47.72/login'
          employeesLogin(this.ruleForm2).then((res) => {
            if(res.data.tatus==1){
              localStorage.setItem('userinfo', JSON.stringify(res.data))
              alert("员工姓名"+res.data.employeeName+"员工工号"+res.data.employeeId)
               _this.$store.commit('SAVE_USERINFO',res.data)
               _this.$router.push('/home/Pos')
            }else{
              alert("账号和密码比匹配！")
            }
          })
        } else {
          return false
        }
      })
    }
  }
}
</script>



